/* global.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

:root {
  --primary: #1f3e72;
  --secondary: rgba(255, 255, 255, 0.78);
  --black: #131110;
  --blue-gradient: linear-gradient(97.05deg, #4066ff 3.76%, #2949c6 100%);
  --orange-gradient: linear-gradient(270deg, #ffb978 0%, #ff922d 100%);
  --blue: #4066ff;
  --lightBlue: #eeeeff;
  --shadow: 0px 23px 21px -8px rgba(136, 160, 255, 0.25);
}

.c-container
{
    justify-content: space-around;
    gap: 1rem;
}

a {
  color: inherit;
  text-decoration: none;
}
.paddings {
  padding: 1.5rem;
}
/* 
.innerWidth {
  width: 100%;
} */

body {
  font-family: 'Poppins', sans-serif;
}


.primaryText
{
  color: #1f3e72;
  font-weight: bold;
  font-size: 2rem;
}


.icon
{
    padding: 10px;
    background-color: #eeeeff;
    border-radius: 5px;
}

.icon svg
{
    fill: var(--blue);
}

.secondaryText 
{
  color: rgb(140 139 139);
  font-size: 1.1rem;
}


.flexStart {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.flexCenter {
  display: flex;
  row-gap: 2rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.button
{
  font-weight: 500;
  padding: 0.6rem 1.4rem;
  color: white;
  background: var(--blue-gradient);
  border: none;
  border-radius: 4px;
  transition: all 300ms ease-in;
}

.flexColS
{
  display: flex;
  justify-content: center;
}

.flexColStart 
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

  .flexColCenter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

.text
{
    color: aquamarine;
    font-size: 18px;
    opacity: 0.8;  
}

/* Outras regras de estilo globais */
